<template>
  <div class="user">
    <!-- 导航栏 -->
    <van-nav-bar placeholder fixed border>
      <template #right>
        <van-icon name="cross" size="26" color="white" @click="jumpPage({name:'Home'})" />
      </template>
    </van-nav-bar>

    <!-- 已登录页面 -->
    <div v-if="isLogin">
      <div class="login-header">
        <div class="img-box">
          <img class="img-auto" :src="userImg" alt />
        </div>
        <div class="user-info">
          <h1>昵称：{{loginName}}</h1>
        </div>
      </div>

      <div class="login-list">
        <ul>
          <li @click="show=!show">
            <van-icon name="user-o" size="26" color="#ccc" />
            <span>账户管理</span>
          </li>
        </ul>
      </div>

      <!-- 弹窗 -->
      <van-popup
        class="popup-box"
        v-model="show"
        position="bottom"
        :style="{ height: '28%' }"
        closeable
        round
      >
        <ul>
          <li @click="quitLogin()">
            <span>退出账户</span>
          </li>
          <li @click="delUser()">
            <span>注销账户</span>
          </li>
        </ul>
      </van-popup>
    </div>

    <!-- 未登录页面 -->
    <div v-else>
      <van-tabs v-model="active">
        <van-tab title="登录">
          <van-form @submit="login()">
            <van-field
              v-model="loginData.username"
              name="账户名"
              label="账户名"
              placeholder="账户名"
              :rules="[{ required: true, pattern:/^\w{1,16}$/, message: '字母、数字和下划线, 最多16位' }]"
            />
            <van-field
              v-model="loginData.password"
              type="password"
              name="密码"
              label="密码"
              placeholder="密码"
              :rules="[{ required: true, pattern:/^\w{6,16}$/, message: '字母、数字和下划线, 6-16位' }]"
            />
            <div style="margin: 16px;">
              <van-button round block color="#D43D3D" native-type="submit">登录</van-button>
            </div>
          </van-form>
        </van-tab>

        <van-tab title="注册">
          <van-form @submit="register()">
            <van-field
              v-model="registerData.name"
              name="昵称"
              label="昵称"
              placeholder="昵称"
              :rules="[{ required: true, pattern:/^\S{1,16}$/, message: '除了空白字符（例如：空格、回车）, 最多16位' }]"
            />
            <van-field
              v-model="registerData.username"
              name="账户名"
              label="账户名"
              placeholder="账户名"
              :rules="[{ required: true, pattern:/^\w{1,16}$/, message: '字母、数字和下划线, 最多16位' }]"
            />
            <van-field
              v-model="registerData.password"
              type="password"
              name="密码"
              label="密码"
              placeholder="密码"
              :rules="[{ required: true, pattern:/^\w{6,16}$/, message: '字母、数字和下划线, 6-16位' }]"
            />
            <div style="margin: 16px;">
              <van-button round block color="#D43D3D" native-type="submit">注册</van-button>
            </div>
          </van-form>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import "@/assets/css/user.less";

// 引入 mapState 插件
import { mapState } from "vuex";

export default {
  data() {
    return {
      active: 0,
      show: false,

      // 是否登录
      // isLogin: false,

      // 登录后昵称
      // loginName: "",

      // 登录后头像
      // userImg: "",

      // 登录信息
      loginData: {
        username: "",
        password: ""
      },

      // 注册信息
      registerData: {
        username: "",
        password: "",
        name: ""
      }
    };
  },

  created() {
    // 查询是否登录
    this.$store.dispatch("queryLogin");
  },

  computed: {
    // 获得state属性
    ...mapState(["isLogin", "loginName", "userImg"])
  },

  methods: {
    // 正则验证表单
    regVal(data) {
      let regData = {
        username: {
          reg: /^\w{1,16}$/,
          msg: `字母、数字和下划线, 最多16位`
        },
        password: {
          reg: /^\w{6,16}$/,
          msg: `字母、数字和下划线, 6-16位`
        },
        name: {
          reg: /^\S{1,16}$/,
          msg: `除了空白字符（例如：空格、回车）, 最多16位`
        }
      };

      for (let k in regData) {
        if (data[k]) {
          if (!regData[k].reg.test(data[k])) {
            this.$toast.fail({
              message: regData[k].msg,
              forbidClick: true,
              //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
              duration: 2000
            });
            return false; // 验证不通过
            break;
          }
        }
      }

      return true; //验证通过
    },

    // 注册
    register() {
      if (this.regVal(this.registerData)) {
        this.$toast.loading({
          message: "注册中...",
          forbidClick: true,
          //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
          duration: 0
        });

        this.axios({
          url: "http://www.xcodeyun.cn:10620/add",
          method: "post",
          data: {
            username: this.registerData.username,
            password: this.registerData.password,
            starArr: [],
            shopArr: [],
            name: this.registerData.name
          }
        })
          .then(res => {
            if (res.data.code == 200) {
              this.$toast.success("注册成功");
              // 跳转到登录
              this.active = 0;

              for (let k in this.registerData) {
                this.registerData[k] = "";
              }
            } else if (res.data.code == 205) {
              this.$toast.fail("该账户名已经存在");
            } else {
              this.$toast.fail("出错了:" + res.data.code);
            }
          })
          .catch(err => {
            this.$toast.fail("注册失败");
          });
      } else {
        this.$toast.fail("格式有误");
      }
    },

    // 登录
    login() {
      if (this.regVal(this.loginData)) {
        this.$toast.loading({
          message: "登录中...",
          forbidClick: true,
          //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
          duration: 0
        });

        this.axios({
          url: "http://www.xcodeyun.cn:10620/find",
          method: "post",
          data: {
            username: this.loginData.username,
            password: this.loginData.password
          }
        })
          .then(res => {
            if (res.data.code == 200) {
              // 登录提示
              this.$toast.success("登录成功");

              // 改变登录状态 跳转到已登录界面
              this.$store.commit("setIsLogin", true);

              // 存储登录状态
              localStorage.setItem("username", this.loginData.username);
              localStorage.setItem("password", this.loginData.password);

              // 获取头像
              // this.userImg = res.data.data.userImg;
              localStorage.setItem("userImg", res.data.data.userImg);

              // 获取昵称
              // this.loginName = res.data.data.name;
              localStorage.setItem("loginName", res.data.data.name);

              // 添加头像
              this.$store.commit("getUserImg");

              //
            } else if (res.data.code == 202) {
              this.$toast.fail("用户名或密码有误");
            } else {
              this.$toast.fail("出错了:" + res.data.code);
            }
          })
          .catch(err => {});
      } else {
        this.$toast.fail("格式有误");
      }
    },

    // 跳转页面
    jumpPage(to) {
      this.$router.push(to);
      this.$toast.clear();
    },

    //退出账户
    quitLogin() {
      // 删除登录状态
      localStorage.removeItem("username");
      localStorage.removeItem("password");
      localStorage.removeItem("userImg");
      // 刷新当前路由
      this.$router.go({ name: "User" });
      // 查询登录状态
      this.$store.dispatch("queryLogin");
    },

    // 注销账户
    delUser() {
      // 获取当前登录账户
      let uname = localStorage.getItem("username");
      let pword = localStorage.getItem("password");

      this.$toast.loading({
        message: "登录中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "post",
        url: "http://www.xcodeyun.cn:10620/remove",
        data: {
          username: uname,
          password: pword
        }
      })
        .then(res => {
          if (res.data.code == 200) {
            this.$toast.success("注销成功");
          } else {
            this.$toast.fail("发生错误 " + res.data.code);
          }
          //

          // 退出登录状态
          this.quitLogin();
        })
        .catch(err => {
          this.$toast.fail("发生错误");
        });
    }
  }
};
</script>

<style lang="less" scoped>
</style>